<script>
import DropdownMenu from './components/dashboard/DropdownMenu.vue';

export default {
  components: { DropdownMenu },
  name: 'App',
  computed: {
    currentViewComponent() {
      return this.currentView;
    },
    deviceList() {
      console.log('DeviceList getter/setter');
      return this.$root.$data.dataStore;
    },
  },
  methods: {
    async logout() {
      await fetch('/logout')
        .then((response) => {
          if (response.status === 404) {
            throw new Error('No data avalaible');
          }
          this.errorMessage = '';
          return response.json();
        })
        .then((response) => {
          window.location.href = response.url;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    menuItemClicked(menuItem) {
      switch (menuItem) {
        case 'Device':
          this.$router.push({ name: 'DeviceChart' });
          break;
        case 'Group':
          this.$router.push({ name: 'GroupChart' });
          break;
        case 'Devices':
          this.$router.push({ name: 'devices' });
          break;
        case 'Groups':
          this.$router.push({ name: 'groups' });
          break;
        default:
          break;
      }
    },
  },
  created() {

  },
  data() {
    return {
      currentView: 'UVCDeviceList',
      showBurgerMenu: false,
    };
  },
};
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

.slideTop-enter-active,
.slideTop-leave-active {
  @apply duration-200;
}

.slideTop-enter-to, .slideTop-leave {
   transform: translateY(0);
}

.slideTop-enter, .slideTop-leave-to {
   transform: translateY(-100%);
}
